var div: HTMLDivElement = document.createElement('div');
div.style.border = "1px solid red";
div.style.height = "100px";
div.style.width = "100px";
div.id = "demo";
document.body.appendChild(div);
var dragging: boolean = false;//设置跟随移动条件（鼠标按下跟随，鼠标抬起不跟随）
//记录鼠标上一次位置（因为当鼠标第一次点击在div里时，div也移动，所以移动的
//应该是鼠标位置差值）
var lastX;
var lastY;
div.onmousedown = function (e) {
    //鼠标按下跟随
    dragging = true;
    //将鼠标第一次点击位置设置为第一次上一次位置
    lastX = e.clientX;
    lastY = e.clientY;
}
div.onmouseup = function () {
    //鼠标抬起解除跟随
    dragging = false;
}
document.body.onmousemove = function (x) {
    console.log(x);
    console.log(lastX, lastY);
    console.log(x.clientX, x.clientY);
    if (dragging === true) {
        //输出当下位置
        console.log("offset", div.offsetLeft, div.offsetTop);
        //输出鼠标移动位置与上一次位置差值
        console.log("delta", x.clientX - lastX, x.clientY - lastY);
        //div绝对位置为当下位置与差值的和
        div.style.top = div.offsetTop + x.clientY - lastY + 'px';
        div.style.left = div.offsetLeft + x.clientX - lastX + 'px';
        //更新上一次位置
        lastX = x.clientX;
        lastY = x.clientY;
    }

}
//但是父容器一定要够大，如果不用父容器用div，当鼠标过快时，浏览器跟不上鼠标，
//鼠标移到了div外面，则托动不了了。